import React, { useState, useEffect } from 'react';

const PlaybookComponent = ({ selectedDb, databases }) => {
  const [playbooks, setPlaybooks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  //   
  const fetchPlaybooks = async () => {
    setLoading(true);
    try {
      const response = await fetch('/api/playbooks');
      if (response.ok) {
        const playbooksData = await response.json();
        setPlaybooks(playbooksData);
      } else {
        console.error('   ');
        setError('     .');
      }
    } catch (error) {
      console.error('   :', error);
      setError('     .');
    } finally {
      setLoading(false);
    }
  };

  //  
  const handleRunPlaybook = async (playbookName) => {
    if (!selectedDb) {
      setError('  .');
      return;
    }

    setLoading(true);
    try {
      const formData = new FormData();
      formData.append('playbook_name', playbookName);
      formData.append('db_name', selectedDb);

      const response = await fetch('/playbooks/run', {
        method: 'POST',
        body: formData
      });

      if (response.ok) {
        setSuccess(` "${playbookName}"  . AI   .`);
        setError('');
        
        // AI   
        setTimeout(() => {
          window.location.href = '/nl2sql';
        }, 2000);
      } else {
        setError('  .');
      }
    } catch (error) {
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchPlaybooks();
  }, []);

  return (
    <div className="playbook-component">
      <div className="section-header">
        <h2> </h2>
        <p>      .</p>
      </div>

      {/*   */}
      {error && (
        <div className="alert alert-error">
          <span> {error}</span>
          <button onClick={() => setError('')} className="close-btn"></button>
        </div>
      )}
      
      {success && (
        <div className="alert alert-success">
          <span> {success}</span>
          <button onClick={() => setSuccess('')} className="close-btn"></button>
        </div>
      )}

      {/* DB   */}
      {!selectedDb && (
        <div className="alert alert-warning">
          <span>     .</span>
        </div>
      )}

      {/*   */}
      <div className="playbook-grid">
        {loading ? (
          <div className="loading-state">
            <div className="loading-spinner"></div>
            <p>  ...</p>
          </div>
        ) : playbooks.length === 0 ? (
          <div className="empty-state">
            <p>   .</p>
            <p>  playbooks.json  .</p>
          </div>
        ) : (
          playbooks.map((playbook, index) => (
            <div key={index} className="playbook-card">
              <div className="playbook-header">
                <h3>{playbook.name}</h3>
                <span className="playbook-category">{playbook.category}</span>
              </div>
              
              <div className="playbook-description">
                <p>{playbook.description}</p>
              </div>
              
              <div className="playbook-steps">
                <h4> :</h4>
                <ol>
                  {playbook.steps.map((step, stepIndex) => (
                    <li key={stepIndex}>
                      <strong>{step.title}</strong>
                      <p>{step.prompt}</p>
                    </li>
                  ))}
                </ol>
              </div>
              
              <div className="playbook-actions">
                <button
                  onClick={() => handleRunPlaybook(playbook.name)}
                  className="btn btn-primary"
                  disabled={loading || !selectedDb}
                >
                  {loading ? ' ...' : ' '}
                </button>
                
                <div className="playbook-info">
                  <span> : {playbook.estimatedTime || '1-2'}</span>
                  <span>: {playbook.difficulty || ''}</span>
                </div>
              </div>
            </div>
          ))
        )}
      </div>

      {/*   */}
      <div className="card">
        <h3>  </h3>
        <div className="usage-guide">
          <ol>
            <li> </li>
            <li>  </li>
            <li>" "  </li>
            <li>AI    </li>
            <li> AI    </li>
          </ol>
        </div>
      </div>
    </div>
  );
};

export default PlaybookComponent; 